import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { FilterHelpers } from '@hackjunction/shared'

import * as DashboardSelectors from 'redux/dashboard/selectors'

import Divider from 'components/generic/Divider'
import AttendeeTable from 'components/tables/AttendeeTableUserSide'
import FilterGroupMenu from 'components/filters/FilterGroupMenu'
import { Table } from 'antd'

export default () => {
    const registrations = useSelector(DashboardSelectors.registrations)
    const registrationsLoading = useSelector(
        DashboardSelectors.registrationsLoading,
    )

    const [filters, setFilters] = useState([])
    const filtered = FilterHelpers.applyFilters(registrations, filters)
    // console.log('registrations', filtered)
    useEffect(() => {
        async function appendCustomAnswers() {
            for (let i = 0; i < filtered.length; i++) {
                const customAnswers = await filtered[i].answers.CustomAnswers
                // console.log('customAnswers', customAnswers)
                filtered[i].customAnswers = {
                    // ...filtered[i].answers,
                    ...customAnswers.reduce((acc, cur) => {
                        acc[cur.key] = cur.value
                        return acc
                    }, {}),
                }
            }
        }
        appendCustomAnswers()
    }, [filtered])

    return (
        <>
            <FilterGroupMenu onChange={setFilters} />
            <Divider size={1} />
            <AttendeeTable
                attendees={filtered}
                loading={registrationsLoading}
            />
        </>
    )
}
