import React, { useState } from 'react'

import { useSelector } from 'react-redux'
import {
    Grid,
    Typography,
    FormControl,
    Select,
    MenuItem,
    InputLabel,
} from '@material-ui/core'

import * as DashboardSelectors from 'redux/dashboard/selectors'

import GradientBox from 'components/generic/GradientBox'

import { useTranslation } from 'react-i18next'
import EventTimeline from 'pages/_events/slug/default/EventTimeline'

export default () => {
    const event = useSelector(DashboardSelectors.event)
    const [region, setRegion] = useState('auto') // Default to auto-detect

    const handleRegionChange = event => {
        setRegion(event.target.value)
    }

    if (!event || !event.eventTimeline) {
        return null // Don't render anything if event or timeline isn't available
    }

    return (
        <Grid item xs={4}>
            <GradientBox color="theme_white" p={3}>
                <Grid container spacing={2}>
                    <Grid item xs={8}>
                        <Typography variant="button" gutterBottom>
                            Event Timeline
                        </Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <FormControl variant="outlined" size="small" fullWidth>
                            <InputLabel id="region-select-label">
                                Region
                            </InputLabel>
                            <Select
                                labelId="region-select-label"
                                id="region-select"
                                value={region}
                                onChange={handleRegionChange}
                                label="Region"
                            >
                                <MenuItem value="auto">Auto-detect</MenuItem>
                                <MenuItem value="americas">Americas</MenuItem>
                                <MenuItem value="epac">
                                    EPAC (Europe/Africa)
                                </MenuItem>
                                <MenuItem value="apac">
                                    APAC (Asia/Pacific)
                                </MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>
                <EventTimeline
                    event={event}
                    accentColor={event.theme.accentColor}
                    textColor={event.theme.sidebarTextColor}
                    forcedRegion={region !== 'auto' ? region : null}
                />
            </GradientBox>
        </Grid>
    )
}
