import Typography from '@material-ui/core/Typography'
import Scoring from 'pages/_dashboard/slug/rating/Scoring'
import React from 'react'
import LoadingSpinner from './loadingCircle'

const RenderContent = ({
    projectsByRating,
    setSelected,
    user,
    scores,
    event,
    ranked,
}) => {
    if (!projectsByRating) return <LoadingSpinner />
    if (projectsByRating.length === 0)
        return ranked ? (
            <div>
                The reviewing process is currently underway. Upon its
                conclusion, the list of winners will be showcased on this page.
            </div>
        ) : (
            <div>
                At present, no projects have been submitted. Please revisit at a
                later time to assess and rate your preferred projects.
            </div>
        )
    return (
        <div>
            {projectsByRating.map((project, index) => {
                return (
                    <div
                        onClick={() => {
                            setSelected(project)
                        }}
                        className="lg:flex hover:cursor-pointer items-center justify-between bg-[#A9EFF2] text-gray-800 w-full mb-4 p-4 rounded-lg "
                    >
                        <div className="flex flex-col mb-2 lg:mb-0 justify-center items-center lg:flex-row">
                            {ranked && (
                                <div className="mr-8 text-xl font-bold">
                                    {index + 1 + ')'}
                                </div>
                            )}
                            <img
                                src={
                                    (project.images[0] &&
                                        project.images[0].url) ||
                                    'https://thumbs.dreamstime.com/b/no-image-available-icon-flat-vector-no-image-available-icon-flat-vector-illustration-132482953.jpg'
                                }
                                alt="project"
                                className="w-full max-w-md lg:w-32 lg:h-32 mr-0 lg:mr-2 object-cover rounded-md"
                            />
                            <div className="flex flex-col justify-start mb-2 lg:mb-0">
                                <Typography variant="body1">
                                    <h3 className="text-gray-800 text-3xl text-left">
                                        {project.name}
                                    </h3>
                                </Typography>
                                <Typography variant="body1">
                                    <h3 className=" text-gray-800 text-lg font-bold">
                                        {project.punchline}
                                    </h3>
                                </Typography>
                                <Typography variant="body1">
                                    <h3 className=" text-gray-800 text-md text-left">
                                        {project.description}
                                    </h3>
                                </Typography>
                                {/* <Typography variant="body1">
                              {project.rating}
                          </Typography> */}
                                <Typography variant="body1">
                                    <div className="flex text-left">
                                        <div className="mr-2">
                                            Technologies:
                                        </div>
                                        {project.technologies.map(
                                            (tech, index) => {
                                                if (
                                                    index ===
                                                    project.technologies
                                                        .length -
                                                        1
                                                ) {
                                                    return (
                                                        <div className="mr-2">
                                                            {tech}
                                                        </div>
                                                    )
                                                }
                                                return (
                                                    <div className="mr-2">
                                                        {tech},
                                                    </div>
                                                )
                                            },
                                        )}
                                    </div>
                                </Typography>
                            </div>
                        </div>
                        <Scoring
                            project={project}
                            user={user}
                            scores={scores}
                            slug={event.slug}
                            event={event}
                        />
                    </div>
                )
            })}{' '}
        </div>
    )
}

export default RenderContent
