import React from 'react'
import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Box } from '@material-ui/core'
import * as AuthSelectors from 'redux/auth/selectors'

import PageHeader from 'components/generic/PageHeader'
import PageWrapper from 'components/layouts/PageWrapper'

import * as DashboardSelectors from 'redux/dashboard/selectors'
import * as DashboardActions from 'redux/dashboard/actions'
import * as UserSelectors from 'redux/user/selectors'
import TeamsService from 'services/teams'

import JoinTeam from './JoinTeam'
import EditTeam from './EditTeam'

import { useTranslation } from 'react-i18next'

export default () => {
    const { t } = useTranslation()
    const dispatch = useDispatch()
    const user = useSelector(AuthSelectors.idTokenData)
    const userProfile = useSelector(UserSelectors.userProfile)
    const event = useSelector(DashboardSelectors.event)
    const hasTeam = useSelector(DashboardSelectors.hasTeam)
    const team = useSelector(DashboardSelectors.team)
    const teamLoading = useSelector(DashboardSelectors.teamLoading)
    const eventLoading = useSelector(DashboardSelectors.eventLoading)
    const teams = useSelector(DashboardSelectors.teams)
    console.log(userProfile)

    useEffect(() => {
        console.log('useEffect')
        dispatch(DashboardActions.getTeams(event.slug))
        // console.log(user.sub)
        // const response = TeamsService.getTeams(user.sub, event.slug)
        // console.log(response)
    }, [dispatch, event.slug, team])

    const loading = teamLoading || eventLoading

    const renderAddButton = () => {
        if (user && userProfile) {
            // if (userProfile.role === 'Advisor') {
            if (user.roles.includes('Advisor')) {
                return (
                    <div>
                        {/* <EditTeam /> */}
                        <JoinTeam />
                    </div>
                )
            } else {
                console.log('else')
                if (hasTeam) {
                    return <EditTeam team={team} />
                } else {
                    return <JoinTeam />
                }
            }
        }
    }

    return (
        <Box>
            <PageHeader
                heading="Team"
                subheading={
                    loading
                        ? ''
                        : t('Team_configure_', {
                              event: event.name,
                          })
                }
            />
            <Box mt={2} />
            <PageWrapper loading={loading}>{renderAddButton()}</PageWrapper>
            {userProfile && userProfile.role === 'Advisor' && (
                <div>
                    <h1 className="text-lg font-bold">Teams</h1>

                    {teams && teams.length > 0 ? (
                        teams.map(team => {
                            console.log(team)
                            return (
                                <div className="mb-8">
                                    <EditTeam team={team} />
                                </div>
                            )
                        })
                    ) : (
                        <p>No teams</p>
                    )}
                </div>
            )}
        </Box>
    )
}
