import React, { useState, useCallback, useMemo, useEffect } from 'react'
import moment from 'moment'
import { push } from 'connected-react-router'
import { useSelector, useDispatch } from 'react-redux'
import { useLocation } from 'react-router-dom'

import { Box } from '@material-ui/core'
import Rating from '@material-ui/lab/Rating'
import StatusBadge from 'components/generic/StatusBadge'
import Tag from 'components/generic/Tag'

import * as OrganiserSelectors from 'redux/organiser/selectors'
import EditRegistrationModal from 'components/modals/EditRegistrationModal'
import BulkEditRegistrationModal from 'components/modals/BulkEditRegistrationModal'
import BulkEmailModal from 'components/modals/BulkEmailModal'

import { Table, Filters, Sorters } from 'components/generic/_Table'
// import { CSVLink } from 'react-csv'
// import { dietaryRestrictions } from '@hackjunction/shared/constants/misc'

export default ({
    emptyRenderer,
    loading,
    attendees = [],
    footer = null,
    title = 'Participants',
    minimal = false,
}) => {
    //    console.log('attendees', attendees)

    const filteredAttendees = useMemo(() => {
        return attendees.filter(attendee => {
            if (!attendee) return false
            if (!attendee.answers) return false
            return attendee.answers.email
        })
    }, [attendees])

    const dispatch = useDispatch()
    const location = useLocation()
    const searchParams = new URLSearchParams(location.search)
    const organiserProfilesMap = useSelector(OrganiserSelectors.organisersMap)
    const event = useSelector(OrganiserSelectors.event)

    const query = new URLSearchParams(location.search)
    const hasModal = query.has('modal')
    const activeModal = query.get('modal')
    const [selected, setSelected] = useState([])
    const [data, setData] = useState([])

    const openSingleEdit = useCallback(
        row => {
            const search = `?${new URLSearchParams({
                modal: 'edit',
                id: row.original.user,
            }).toString()}`
            dispatch(push({ search }))
        },
        [dispatch],
    )

    const openBulkEmail = useCallback(
        selectedRows => {
            setSelected(selectedRows)
            const search = `?${new URLSearchParams({
                modal: 'bulkEmail',
            })}`
            dispatch(push({ search }))
        },
        [dispatch],
    )

    const openBulkEdit = useCallback(
        selectedRows => {
            setSelected(selectedRows)
            const search = `?${new URLSearchParams({
                modal: 'bulkEdit',
            })}`
            dispatch(push({ search }))
        },
        [dispatch],
    )
    const columns = useMemo(() => {
        return [
            {
                Header: '#',
                accessor: (row, index) => {
                    return index + 1
                },
                ...Sorters.Numeric,
                id: 'index',
            },
            {
                Header: 'First name',
                accessor: 'answers.firstName',
                ...Sorters.Alphabetic,
                ...Filters.ContainsSearch,
            },
            {
                Header: 'Last name',
                accessor: 'answers.lastName',
                ...Sorters.Alphabetic,
                ...Filters.ContainsSearch,
            },
            {
                Header: 'Email',
                accessor: 'answers.email',
                ...Sorters.Alphabetic,
                ...Filters.ContainsSearch,
            },
            {
                Header: 'Expertise',
                accessor: 'answers.expertise',
                ...Sorters.Alphabetic,
                ...Filters.ContainsSearch,
            },
            {
                Header: 'Status',
                accessor: 'status',
                ...Filters.MultipleSelect,
                ...Sorters.Alphabetic,
                Cell: ({ cell: { value } }) => <StatusBadge status={value} />,
            },
            {
                Header: 'Rating',
                accessor: 'rating',
                ...Filters.MultipleSelect,
                ...Sorters.Numeric,
                Cell: ({ cell: { value } }) =>
                    value ? (
                        <Rating size="small" value={value} readOnly />
                    ) : (
                        'Not rated'
                    ),
            },
            {
                Header: 'Tags',
                accessor: 'tags',
                ...Sorters.ArrayLength,
                Cell: ({ cell: { value } }) => {
                    console.log('tags', value)
                    if (!value) {
                        // console.log('no tags', value)
                        return 'No tags'
                    } else {
                        return (
                            <>
                                {/* <Tag color="#ca8a04" label="Advisor" /> */}
                                {value.map((tag, index) => {
                                    return (
                                        <Box key={index} ml="3px" mt="3px">
                                            <Tag color="green" label={tag} />
                                        </Box>
                                    )
                                })}
                            </>
                        )
                    }
                },
            },
            {
                Header: 'Created at',
                accessor: 'createdAt',
                ...Sorters.DateTime,
                Cell: ({ cell: { value } }) =>
                    moment(value).format('MMM Do YYYY HH:mm:ss'),
            },
            // {
            //     Header: 'Assigned to',
            //     accessor: 'assignedTo',
            //     ...Sorters.Alphabetic,
            //     Cell: ({ cell: { value } }) => {
            //         let text
            //         if (!value) {
            //             text = 'No one'
            //         } else if (organiserProfilesMap.hasOwnProperty(value)) {
            //             const user = organiserProfilesMap[value]
            //             text = `${user.firstName} ${user.lastName}`
            //         } else {
            //             text = '???'
            //         }
            //         return text
            //     },
            // },
        ]
    }, [organiserProfilesMap])

    // const data = useMemo(async () => {
    //     await Promise.all(
    //         selected.map(async (s, index) => {
    //             // console.log('s', s)
    //             const output = await flattenObject(s.original, index)
    //             return output
    //         }),
    //     )
    // }, [selected])

    // console.log(data)

    useEffect(() => {
        const fetchData = async () => {
            const data = await Promise.all(
                selected.map(async (s, index) => {
                    // console.log('s', s)
                    const output = await flattenObject(s.original, index)
                    return output
                }),
            )
            setData(data)
            console.log('data from use effect', data)
        }
        if (selected.length > 0) {
            fetchData()
        }
    }, [selected])

    // TODO move somewhere else
    function flattenObject(ob, index) {
        const answers = ob.answers
        let customAnswers = ob.customAnswers
        // if (
        //     selected &&
        //     selected.length > 0 &&
        //     selected[index] &&
        //     selected[index].original &&
        //     selected[index].original.answers
        // ) {
        //     customAnswers = await selected[index].original.answers.CustomAnswers
        // }
        var toReturn = {
            '#': index + 1,
            'First name': answers.firstName,
            'Last name': answers.lastName,
            Email: answers.email,
            Status: ob.status,
            expertise: answers.expertise.join(', '),
            skills: answers.skills.join(', '),
            'Industries of Interest': answers.industriesOfInterest.join(', '),

            Tags: ob.tags?.join(', '),
            // object to array to object
            ...customAnswers,
            'Dietary Restrictions': answers.dietaryRestrictions?.join(', '),
            'Spoken Languages': answers.spokenLanguages?.join(', '),
            'Themes of Interest': answers.themesOfInterest.join(', '),

            'Created at': moment(ob.createdAt).format('MMM Do YYYY HH:mm:ss'),
        }

        return toReturn
    }

    const exportregistrations = selectedRows => {
        // console.log('selected', selectedRows)
        setSelected(selectedRows)
    }

    const resetSearch = useCallback(() => {
        dispatch(push({ search: '' }))
    }, [dispatch])

    useEffect(() => {
        //Verify the query parameters
        switch (activeModal) {
            case 'bulkEmail':
            case 'bulkEdit':
                if (selected.length === 0) {
                    resetSearch()
                }
                break
            case 'edit':
                if (!query.has('id')) {
                    resetSearch()
                }
                break
            default:
                break
        }
    }, [activeModal, resetSearch, hasModal, query, selected.length])

    // console.log(columns)
    return (
        <>
            <EditRegistrationModal
                registrationId={
                    activeModal === 'edit' ? searchParams.get('id') : undefined
                }
                onClose={resetSearch}
            />
            <BulkEditRegistrationModal
                visible={activeModal === 'bulkEdit'}
                onClose={resetSearch}
                userIds={selected.map(s => s.original.user)}
            />
            <BulkEmailModal
                visible={activeModal === 'bulkEmail'}
                onClose={resetSearch}
                userIds={selected.map(s => s.original.user)}
            />
            <Table
                data={filteredAttendees}
                columns={columns}
                // onRowClick={openSingleEdit}
                // bulkActions={[

                // ]}
            />
        </>
    )
}
