import React, { useCallback, useEffect, useState } from 'react'

import { useRouteMatch, useLocation } from 'react-router'
import { useDispatch, useSelector } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles'
import GroupIcon from '@material-ui/icons/Group'
import DashboardIcon from '@material-ui/icons/Dashboard'
import FingerprintIcon from '@material-ui/icons/Fingerprint'
import FlightTakeoffIcon from '@material-ui/icons/FlightTakeoff'
import AssignmentOutlinedIcon from '@material-ui/icons/AssignmentOutlined'
import StarRateIcon from '@material-ui/icons/StarRate'
import HowToVoteIcon from '@material-ui/icons/HowToVote'
import SidebarLayout from 'components/layouts/SidebarLayout'
import Image from 'components/generic/Image'
import BasicNavBar from 'components/navbars/BasicNavBar'
import PageWrapper from 'components/layouts/PageWrapper'
import DefaultPage from './default'
import TeamPage from './team'
import ProjectPage from './project'
import ReviewingPage from './reviewing'
import RatingPage from './rating'
import FinalistPage from './finalist'
import TravelGrantPage from './travel-grant'
import EventIDPage from './event-id'
import ParticipantPage from './participants'
import * as DashboardSelectors from 'redux/dashboard/selectors'
import * as DashboardActions from 'redux/dashboard/actions'
import * as OrganiserActions from 'redux/organiser/actions'
import * as AuthSelectors from 'redux/auth/selectors'
import * as UserSelectors from 'redux/user/selectors'
import { useTranslation } from 'react-i18next'
import { Box, Button } from '@material-ui/core'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos'
import { push } from 'connected-react-router'

const useStyles = makeStyles(theme => ({
    sidebarTop: {
        padding: theme.spacing(3),
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
    },
    sidebarLogo: {
        width: '100%',
        objectFit: 'contain',
    },
}))

export default () => {
    const { t } = useTranslation()

    const classes = useStyles()
    const match = useRouteMatch()
    const location = useLocation()
    const dispatch = useDispatch()

    const event = useSelector(DashboardSelectors.event)
    const eventLoading = useSelector(DashboardSelectors.eventLoading)
    const registrationLoading = useSelector(
        DashboardSelectors.registrationLoading,
    )
    const hasAdvisor = useSelector(AuthSelectors.hasAdvisor)
    // const userProfile = useSelector(UserSelectors.userProfile)
    // const hasAdvisor = userProfile && userProfile.role === 'Advisor'
    const user = useSelector(AuthSelectors.idTokenData)
    const team = useSelector(DashboardSelectors.team)
    const lockedPages = useSelector(DashboardSelectors.lockedPages)
    const shownPages = useSelector(DashboardSelectors.shownPages)
    const { slug } = match.params
    const [hidden, setHidden] = useState(false)
    // console.log(userProfile)
    /** Update when slug changes */
    useEffect(() => {
        dispatch(DashboardActions.updateEvent(slug))
        dispatch(DashboardActions.updateRegistration(slug))
        dispatch(DashboardActions.updateTeam(slug))
        //TODO dont use OrganiserSelectors here
        dispatch(OrganiserActions.updateProjects(slug))
        dispatch(OrganiserActions.updateGavelProjects(slug))
        dispatch(OrganiserActions.updateRankings(slug))
        dispatch(OrganiserActions.generateResults(slug)) // TODO do we need to get results always?
        dispatch(DashboardActions.updateRegistrationsForEvent(slug))
        dispatch(OrganiserActions.updateRegistrationsForEvent(slug))
        dispatch(OrganiserActions.updateTeamsForEvent(slug))
        dispatch(DashboardActions.updateTeamsForEvent(slug))
        dispatch(OrganiserActions.getRankedProjects(slug))
    }, [slug, dispatch])

    /** Update project when team changes */
    useEffect(() => {
        dispatch(DashboardActions.updateProjects(slug))
        dispatch(DashboardActions.updateProjectScores(slug))
        console.log(team)
        if (hasAdvisor) {
            setHidden(true)
        } else {
            setHidden(false)
        }
    }, [slug, team, dispatch])

    const onBack = useCallback(() => {
        dispatch(push('/account'))
    }, [dispatch])

    // throw new Error('This is an error')

    return (
        <PageWrapper
            loading={eventLoading || registrationLoading}
            wrapContent={false}
        >
            <Box style={{ zIndex: '2000', position: 'fixed' }}>
                <Button style={{ color: 'white' }} onClick={onBack}>
                    <ArrowBackIosIcon
                        style={{ fontSize: '14px', color: 'white' }}
                    />
                    Back
                </Button>
            </Box>
            {console.log(hidden)}
            <SidebarLayout
                baseRoute={match.url}
                location={location}
                sidebarTopContent={
                    <div className={classes.sidebarTop}>
                        <Image
                            className={classes.sidebarLogo}
                            publicId={
                                event && event.logo ? event.logo.publicId : ''
                            }
                            transformation={{
                                width: 200,
                            }}
                        />
                    </div>
                }
                topContent={<BasicNavBar />}
                routes={[
                    {
                        key: 'dashboard',
                        path: '',
                        exact: true,
                        icon: <DashboardIcon />,
                        label: t('Dashboard_'),
                        component: DefaultPage,
                    },
                    {
                        key: 'participants',
                        path: '/participants',
                        exact: false,
                        icon: <GroupIcon />,
                        label: 'Participants',
                        // locked: lockedPages.team,
                        // lockedDescription: 'Can not access participants yet.',
                        component: ParticipantPage,
                    },

                    // {
                    //     key: 'finals',
                    //     path: '/finalist-voting',
                    //     exact: true,
                    //     hidden: !shownPages.finalistVoting,
                    //     locked: lockedPages.finalistVoting,
                    //     lockedDescription:
                    //         'Finalist voting closed until peer review is done',
                    //     icon: <HowToVoteIcon />,
                    //     label: 'Finalist voting',
                    //     component: FinalistVotingPage,
                    // },
                    {
                        key: 'finalist',
                        path: '/winners',
                        exact: true,
                        hidden: false,
                        locked: lockedPages.winner,
                        lockedDescription: 'Finalist closed',
                        icon: <HowToVoteIcon />,
                        label: 'Winners',
                        component: FinalistPage,
                    },
                    {
                        key: 'team',
                        path: '/team',
                        exact: true,
                        icon: <GroupIcon />,
                        label: t('Team_'),
                        locked: lockedPages.team,
                        lockedDescription: 'Team editing not open',
                        component: TeamPage,
                    },
                    {
                        key: 'project',
                        path: '/project',
                        exact: true,
                        hidden: hidden,
                        locked: lockedPages.submissions,
                        lockedDescription: 'Submissions not open',
                        // hidden: !shownPages.submissions,
                        icon: <AssignmentOutlinedIcon />,
                        label: t('Project_submissions_'),
                        component: ProjectPage,
                    },
                    {
                        key: 'reviewing',
                        path: '/reviewing',
                        exact: true,
                        hidden: !shownPages.reviewing,
                        locked: lockedPages.reviewing,
                        lockedDescription: 'Reviewing closed',
                        icon: <StarRateIcon />,
                        label: t('Reviewing_'),
                        component: ReviewingPage,
                    },
                    {
                        key: 'rating',
                        path: '/rating',
                        exact: true,
                        hidden: false,
                        locked: lockedPages.winner,
                        lockedDescription: 'Rating closed',
                        icon: <StarRateIcon />,
                        label: t('Rating_'),
                        component: RatingPage,
                    },

                    {
                        key: 'eventid',
                        path: '/event-id',
                        exact: true,
                        hidden: !shownPages.eventID,
                        icon: <FingerprintIcon />,
                        label: 'Event ID',
                        component: EventIDPage,
                    },
                    {
                        key: 'travelgrant',
                        path: '/travel-grant',
                        exact: true,
                        icon: <FlightTakeoffIcon />,
                        hidden: !shownPages.travelGrant,
                        label: 'Travel grant',
                        component: TravelGrantPage,
                    },
                    // {
                    //     key: 'hackerpack',
                    //     path: '/hackerpack',
                    //     exact: true,
                    //     icon: <AmpStoriesIcon />,
                    //     hidden: !shownPages.hackerPack,
                    //     label: t('Hackerpack_'),
                    //     component: HackerpackPage,
                    // },
                    // {
                    //     key: 'challenges',
                    //     path: '/challenges',
                    //     exact: true,
                    //     icon: <FormatListBulletedIcon />,
                    //     label: 'Challenges',
                    //     component: ChallengesIndex,
                    // },
                ]}
            />
        </PageWrapper>
    )
}
