import React from 'react'

import moment from 'moment-timezone'
import Countdown from 'react-countdown-now'
import { push } from 'connected-react-router'
import { useSelector, useDispatch } from 'react-redux'
import { Grid, Typography } from '@material-ui/core'
import { RegistrationStatuses, EventHelpers } from '@hackjunction/shared'
import * as AuthSelectors from 'redux/auth/selectors'

import * as DashboardSelectors from 'redux/dashboard/selectors'
import * as UserSelectors from 'redux/user/selectors'

import Button from 'components/generic/Button'
import GradientBox from 'components/generic/GradientBox'

import { useTranslation } from 'react-i18next'
import { concatAndCapitalize } from '@hackjunction/shared/helpers/utils'

export default () => {
    const { t } = useTranslation()
    const hasAdvisor = useSelector(AuthSelectors.hasAdvisor)
    const dispatch = useDispatch()
    const event = useSelector(DashboardSelectors.event)
    const registration = useSelector(DashboardSelectors.registration)
    const project = useSelector(DashboardSelectors.projects)
    const projectLoading = useSelector(DashboardSelectors.projectsLoading)
    const isSubmissionsUpcoming = useSelector(
        DashboardSelectors.isSubmissionsUpcoming,
    )
    const isSubmissionsPast = useSelector(DashboardSelectors.isSubmissionsPast)
    const userProfile = useSelector(UserSelectors.userProfile)
    // const hasAdvisor = userProfile && userProfile.role === 'Advisor'

    let submissionsStartTimeAlias = 'submissionsStartTime'
    let submissionsEndTimeAlias = 'submissionsEndTime'

    if (userProfile.location) {
        submissionsStartTimeAlias = concatAndCapitalize(
            'submissionsStartTime',
            userProfile.location,
        )
        submissionsEndTimeAlias = concatAndCapitalize(
            'submissionsEndTime',
            userProfile.location,
        )
    }

    if (registration?.status !== RegistrationStatuses.asObject.checkedIn.id)
        return null
    if (EventHelpers.isEventOver(event, moment, userProfile.location))
        return null
    if (projectLoading) return null
    console.log(hasAdvisor)

    if (isSubmissionsUpcoming) {
        return (
            <Grid item xs={12}>
                <GradientBox color="theme_white" p={3}>
                    <Typography variant="button" gutterBottom>
                        {t('Project_submissions_')}
                    </Typography>
                    <Typography variant="h4" gutterBottom>
                        {t('Project_submissions_not_open_')}
                    </Typography>
                    <Typography variant="body1">
                        {t('Project_submissions_upcoming_', {
                            submissionsStartTime: moment(
                                event[submissionsStartTimeAlias],
                            ).format('LLLL'),
                            submissionsDeadline: moment(
                                event[submissionsEndTimeAlias],
                            ).format('LLLL'),
                        })}
                    </Typography>
                </GradientBox>
            </Grid>
        )
    }

    if (isSubmissionsPast) {
        return (
            <Grid item xs={12}>
                <GradientBox color="theme_white" p={3}>
                    <Typography variant="button" gutterBottom>
                        {t('Project_submissions_')}
                    </Typography>
                    <Typography variant="h4" color="secondary" gutterBottom>
                        {t('Project_submissions_closed_')}
                    </Typography>
                    <Typography variant="body1">
                        {t('Project_submissions_closed_text_')}
                    </Typography>
                </GradientBox>
            </Grid>
        )
    }

    // Project submissions are open

    if (!project) {
        if (!hasAdvisor) {
            return (
                <Grid item xs={12}>
                    <GradientBox color="theme_orange" p={3}>
                        <Typography variant="button" gutterBottom>
                            Project submissions
                        </Typography>
                        <Typography variant="h4">
                            Project submissions are open
                        </Typography>
                        <Typography variant="h6" gutterBottom>
                            The submission deadline is in{' '}
                            <Countdown
                                daysInHours
                                date={event[submissionsEndTimeAlias]}
                                renderer={({ formatted }) =>
                                    `${formatted.hours}:${formatted.minutes}:${formatted.seconds}`
                                }
                            />
                        </Typography>
                        <Typography variant="body1" gutterBottom>
                            You haven't yet submitted a project! You should go
                            and submit a draft of your project as soon as you
                            have a rough idea of what you're making. You're free
                            to edit your project submission as many times as you
                            wish until the final submission deadline, and the
                            project won't be visible to others until the end.
                            It'll take you under a minute, we promise.
                        </Typography>
                        <Button
                            variant="containedGrey"
                            color="theme_white"
                            onClick={() =>
                                dispatch(
                                    push(`/dashboard/${event.slug}/project`),
                                )
                            }
                        >
                            Make a draft submission
                        </Button>
                    </GradientBox>
                </Grid>
            )
        }
    }

    return (
        <Grid item xs={12}>
            <GradientBox color="theme_turquoise" p={3}>
                <Typography variant="button" gutterBottom>
                    Project submissions
                </Typography>
                <Typography variant="h4">
                    Project submissions are open!
                </Typography>
                <Typography variant="h6" gutterBottom>
                    The submission deadline is in{' '}
                    <Countdown
                        daysInHours
                        date={event[submissionsEndTimeAlias]}
                        renderer={({ formatted }) =>
                            `${formatted.hours}:${formatted.minutes}:${formatted.seconds}`
                        }
                    />
                </Typography>

                {hasAdvisor ? (
                    <Typography variant="body1" gutterBottom>
                        As an advisor you can't submit the project. But you can
                        guide your team in the project.
                    </Typography>
                ) : (
                    <>
                        <Typography variant="body1" gutterBottom>
                            Great, you've already made a submission! You can
                            make edits to it until the final submission
                            deadline, so make sure to make it as polished as
                            possible!
                        </Typography>
                        <Button
                            variant="containedGrey"
                            color="theme_white"
                            onClick={() =>
                                dispatch(
                                    push(`/dashboard/${event.slug}/project`),
                                )
                            }
                        >
                            Edit your submission
                        </Button>
                    </>
                )}
            </GradientBox>
        </Grid>
    )
}
